import React, { Component } from "react"
import Navbar from "../component/Navbar"
import axios from "axios"
import Details from "../component/Details"

class index extends Component {
  state = {
    selectedCountry: null
  }
  clearSelection = () => {
    this.setState({
      selectedCountry: null
    })
  }
  handleClick = (e,code) => {
    console.log(code);
  }
  renderCountry = each => {
    return (
      <li className="items" key={each.currency} onClick={this.handleClick.bind(each.currency)}>
        {each.country}
      </li>
    )
  }
  render() {
    return (
      <>
        <Navbar />
        <div className="container">
          {this.selectedCountry ? (
            <Details />
          ) : (
            <ul>{this.props.counties.map(each => this.renderCountry(each))}</ul>
          )}
        </div>

        <style jsx="true">
          {`
            .items {
              cursor: pointer;
              background: red;
              width: 200px;
            }
          `}
        </style>
      </>
    )
  }
}
index.getInitialProps = async () => {
  const res = await axios.get(
    "https://api.coindesk.com/v1/bpi/supported-currencies.json"
  )
  return {
    counties: res.data
  }
}
export default index
